
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import Scroll from 'assembly/scroll/scroll'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 20

export default {
components: {
XHeader,
Divider,
Scroll,
Loading,
Datetime,
Actionsheet
},
data () {
return {
disArr: [],
listenScroll: true,
hasMore: true,
pageNo: 1,
time: '',
online: '',
visibility: false,
inited: false,
pullup: true,
typeMenu: {
'': '全部',
'1': '线上充值',
'0': '线下充值',
'2': '工行到账伴侣充值'
},
typeSelected: false,
typeName: '全部'
}
},
methods: {
onCancel () {
this.time = ''
this._getDiscountList()
},
setType (key, item) {
this.online = key
this.typeName = item
this.pageNo = 1
this.disArr = []
this.hasMore = true
this._getDiscountList()
},
loadMore () {
if (!this.hasMore) {
return
}
this.pageNo ++
this._getDiscountList()
},
toDetail (id) {
this.$router.push({
path: '/rechargeDetail',
query: {
id: id
}
})
},
change () {
this.pageNo = 1
this.disArr = []
this.hasMore = true
this._getDiscountList()
},
formatePrice (num) {
return num / 100
},
_getDiscountList () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.inited = true
let data = {
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
},
online: this.online === '0' ? 0 : this.online === '1' ? 1 : this.online
}
if (this.time) {



data.startTime = this.time + '-01'
data.endTime = this.$util.getMonthLastDay(this.time)
}
this.$tupHttp(this, 'front/account/charge/list/v1', 'POST', data).then(res => {
this.inited = false
let disArr = res.data.models.map(item => {
item.amount = this.formatePrice(item.amount)
item.createTime = new Date(item.createTime).format('yyyy-MM-dd hh:mm:ss')
if (item.auditStatus) { // 线下充值才有这个字段
if (item.auditStatus === '8796113240155' || item.auditStatus === '8796113272923') {
item.status = '审核中'
} else if (item.auditStatus === '8796113305691' || item.auditStatus === '8796113371227') {
item.status = '审核未通过'
} else if (item.auditStatus === '8796113338459') {
item.status = '审核通过'
}
} else { // 线上充值
item.status = item.payStatus === 1 ? '充值成功' : '充值失败'
}
return item
})
if (this.pageNo === 1 && !this.disArr.length) {
this.disArr = disArr
} else {
this.disArr = this.disArr.concat(disArr)
}
if (res.data.models.length < PAGESIZE) { // 是否有下一页
this.hasMore = false
}
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
created () {
this._getDiscountList()
}
}



